@import url(https://fonts.googleapis.com/css?family=Sniglet);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: #eaeeb6; }

.App-body {
  border: 2px solid #47476b;
  padding: 5px;
  background: black;
  width: 320px;
  position: relative; }

.formulaScreen {
  font-family: "Sniglet", cursive;
  min-height: 40px;
  font-size: 30px;
  color: #fcdb04;
  text-align: right;
  vertical-align: text-top;
  line-height: 30px;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.outputScreen {
  font-size: 25px;
  font-family: "Sniglet", cursive;
  color: white;
  text-align: right;
  line-height: 35px; }

button {
  position: relative;
  height: 65px;
  width: 80px;
  color: white;
  outline: 1px solid black;
  border: none;
  background: #4d4d4d;
  font-family: "Sniglet", cursive, monospace;
  font-size: 20px;
  cursor: default; }
  button:hover {
    color: black;
    outline: .05em solid grey;
    z-index: 3; }

